export default "bts-slidey {\n  pointer-events: none;\n}\n\nbts-slidey.opened {\n  pointer-events: auto;\n}\n\n/* Overlay */\n\n.slidey-overlay {\n  position: absolute;\n  top: 0;\n  right: 0;\n  left: 0;\n  bottom: 0;\n  background-color: black;\n  transition: opacity cubic-bezier(0.4, 0, 0.2, 1) 0.5s;\n  opacity: 0;\n  z-index: 9999;\n}\n\nbts-slidey.opened > .slidey-overlay {\n  opacity: 0.15;\n}\n\n/* Content */\n\n.slidey-content-wrapper {\n  position: absolute;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  background-color: white;\n  transform: translateX(100%);\n  transition: cubic-bezier(0.4, 0, 0.2, 1) 0.5s;\n  transition-property: transform, width, box-shadow;\n  overflow-y: auto;\n  overflow-x: hidden;\n  z-index: 9999;\n}\n\nbts-slidey.opened > .slidey-content-wrapper {\n  transform: translateX(0%);\n  box-shadow: 0 8px 8px rgba(0,0,0,0.5);\n}\n\nbts-slidey.nested > .slidey-content-wrapper {\n  overflow-y: hidden;\n}\n"